import React, { useMemo, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { FormProvider, useForm } from 'react-hook-form';
import { toast } from 'react-toastify';

import useAuth from '../../hooks/useAuth';
import useLocales from '../../hooks/useLocales';
import { API_AUTH } from '../../config';
import axios from '../../utils/axios';

import Page from '../../components/Page';
import PageBanner from '../../components/wrappers/PageBanner';
import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';
import { BlurModalBox } from '../../components/StyledComponents';

const VerificationEmailModal = ({ email }) => {
    const { t } = useLocales();
    return (
        <>
            <input type="checkbox" id="verification-email-modal-check" className="modal-toggle" />
            <div className="modal">
                <BlurModalBox className="modal-box backdrop-blur-lg">
                    <div className="mb-2">
                        <label
                            htmlFor="verification-email-modal-check"
                            className="btn btn-sm btn-circle absolute right-2 top-5 bg-transparent border-0"
                        >
                            âœ•
                        </label>
                        <p className="font-medium text-base text-white">{t('title.modal.check_inbox')}</p>
                    </div>
                    <hr className="-mx-5 border-stone-500" />
                    <div className="flex flex-col gap-8 px-6 pt-6">
                        <div className="flex flex-col gap-4 justify-center items-center">
                            <svg width="150" height="150" viewBox="0 0 150 150" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <circle cx="75" cy="75" r="75" fill="#5D4B15" />
                                <path d="M107.977 53.0645L77.3253 80.4331C75.9992 81.6118 74.0008 81.6118 72.6747 80.4331L42.0371 53.0371M65.7646 74.2354L42.3311 98.2432M107.956 97.9561L84.2285 74.2285M47 100H103C106.866 100 110 96.866 110 93V58C110 54.134 106.866 51 103 51H47C43.134 51 40 54.134 40 58V93C40 96.866 43.134 100 47 100Z" stroke="#F2B705" strokeWidth="4" />
                                <rect x="108" y="108" width="38" height="38" rx="19" fill="#3EC200" />
                                <path d="M119.083 127L125.417 133.333L134.917 122.25" stroke="white" strokeWidth="3" strokeLinecap="round" strokeLinejoin="round" />
                            </svg>
                            <p className="text-slate-500 text-center" >
                                {t("description.email_sent_before")}
                                <span className="text-white"> {email} </span> {t("description.email_sent_after")}
                            </p>
                        </div>
                    </div>
                </BlurModalBox>
            </div>
        </>
    );
}


export default function ChangeEmail() {
    const { t } = useLocales();
    const navigate = useNavigate();
    const { user, initialize } = useAuth();
    const [loading, setLoading] = useState(false);

    const { register, handleSubmit, watch } = useForm({
        values: useMemo(() => {
            return {
                email: user?.email,
            };
        }, [user?.email])
    });

    const email = watch("email");

    const onSubmit = (data) => {
        if (!user?.emailVerification || data.email !== user?.email) {
            setLoading(true);
            axios.put(API_AUTH.userInfo, data)
                .then((res) => {
                    if (res.status === 200) {
                        initialize();
                        document.querySelector("#verification-email-modal-check").click();
                    } else
                        toast.warning(res.data?.message || res.data?.data);

                    setLoading(false);
                })
                .catch(() => { setLoading(false); })
                .finally(() => {
                    setLoading(false);
                });
        }
    };
    
    return (
        <Page title="Profile">
            <PageBanner>
                <FormProvider>
                    <form onSubmit={handleSubmit(onSubmit)}>
                        <div className="container mb-[275px] px-2.5">
                            <div className="w-full justify-center flex flex-col gap-8 items-center">
                                <div className="flex gap-4 w-full">
                                    <Link to="../" className="text-primary cursor-pointer underline">
                                        {t('title.menu.profile')}
                                    </Link>
                                    <div>/</div>
                                    <div className="grid md:grid-cols-2 text-stone-400">
                                        <p>{t('title.email_address')}</p>
                                    </div>
                                </div>

                                <div className="flex flex-col gap-16 w-full">
                                    <div className="flex flex-col gap-4">
                                        <h3 className="text-4xl md:text-[64px]">{t('title.email_address')}</h3>
                                        <div className="grid md:grid-cols-2 text-stone-400">
                                            <p>{t('description.change_email')}</p>
                                        </div>
                                    </div>
                                    <div className="md:w-1/2 flex gap-4">
                                        <GradientBorderWrapper>
                                            <div className="flex justify-between px-2 items-center text-stone-400">
                                                <input
                                                    type="email"
                                                    required
                                                    className="input w-full h-[62px] text-lg"
                                                    {...register('email')}
                                                />
                                            </div>
                                        </GradientBorderWrapper>
                                        <button type="submit" className={`btn btn-primary px-8 py-[19px] font-normal text-[19px] h-auto rounded-2xl capitalize disabled:bg-secondary ${loading && 'loading'} `} disabled={user?.emailVerification !== false && email === user?.email}>
                                            {t('action.send')}
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <VerificationEmailModal email={email} />
                    </form>
                </FormProvider>
            </PageBanner>
        </Page >
    );
}
