import styled from 'styled-components';

const GradientBorderStyleWrapper = styled.div`
    border-radius: 16px;
    width: 100%;
    cursor: pointer;
    border: 1px;
    border-style: solid;
    border-color: transparent;

    display: inline-flex;
    justify-content: space-between;
    position: relative;
    background: linear-gradient(#1c1c1c, #1c1c1c) padding-box, linear-gradient(to bottom, #828282, #323232) border-box;
`;

export default function GradientBorderWrapper(props) {
    return (
        <GradientBorderStyleWrapper className={props?.className} >
            <div
                className={`w-full ${props?.isMessage && 'bg-primary/30'} rounded-xl ${props?.hoverEvent && 'hover:bg-primary/20'
                    } `}
                onClick={props?.onClick}
            >
                {props.children}
            </div>
        </GradientBorderStyleWrapper>
    );
}

export const Divider = ({ cls = '' }) => <div className={`divider overflow-hidden ${cls}`} style={{ height: 1 }} />;
